@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
.particles{position:fixed;top:0;right:0;bottom:0;left:0;z-index:-1000}.App-header{padding-top:15vh;display:flex;flex-direction:column;align-items:center;justify-content:center;color:white;font-family:'Varela', sans-serif}.avatar{height:325px;border:7px solid #397367;border-radius:50%}.name-headline{margin:2rem 0 0 0;font-size:72px;background-color:#CA4246;background-image:linear-gradient(45deg, #397367, #A0C193);background-size:100%;background-repeat:repeat;-webkit-background-clip:text;-webkit-text-fill-color:transparent;animation:rainbow-text-simple-animation-rev 0.75s ease forwards}.name-headline:hover{animation:rainbow-text-simple-animation 0.5s ease-in forwards}@keyframes rainbow-text-simple-animation-rev{0%{background-size:650%}40%{background-size:650%}100%{background-size:100%}}@keyframes rainbow-text-simple-animation{0%{background-size:100%}80%{background-size:650%}100%{background-size:650%}}.quick-about{font-size:1.5rem;margin-top:.5rem;background-image:linear-gradient(45deg, #397367, #A0C193);background-size:100%;background-repeat:repeat;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.placeholder{font-style:italic;font-size:.75rem;color:#7F6A93}.icon-scroll,.icon-scroll:before{position:absolute;left:50%}.icon-scroll{width:30px;height:50px;margin-left:-15px;bottom:5%;box-shadow:inset 0 0 0 1px #A0C193;border-radius:25px}.icon-scroll:before{content:'';width:5px;height:5px;background:linear-gradient(45deg, #397367, #A0C193);margin-left:-2px;top:8px;border-radius:4px;animation-duration:1.5s;animation-iteration-count:infinite;animation-name:scroll}@keyframes scroll{0%{opacity:1}100%{opacity:0;transform:translateY(25px)}}@media only screen and (max-width: 576px){.App-header{text-align:center}}@media only screen and (max-height: 740px){.icon-scroll{bottom:3%}}@media only screen and (max-height: 688px){.icon-scroll{display:none}}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#181818}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.App{min-height:100vh}

